<template>
  <div class="Index">
		<Header class="header"></Header>
    <Nav class="navBar"></Nav>
		<div :style="currentHeight" class="box">
			<div class="text">
				<article class="context" v-html="compiledMarkdown"></article>
			</div>
		</div>
  </div>
</template>

<script>
	import Header from "./Docs_Header.vue"
	import Nav from "./Docs_NavBar.vue"
	import marked from 'marked'
	import hljs from "highlight.js"
	import 'highlight.js/styles/monokai-sublime.css'

export default {
  name: 'Index',
  data () {
    return {
      currentHeight:{
      	height:''
      },
			routeParams:"introduction",
			articleDetail: "暂无数据",
    }
  },components:{
		Header,
		Nav
	},mounted() {
  	let $Height = window.innerHeight -70;
  	console.log("当前可视高度：",$Height);
  	this.currentHeight.height = $Height+"px";

		// this.$router.push({  //核心语句
		//   path:this.routeParams,   //跳转的路径
		// })

		marked.setOptions({
		  renderer: new marked.Renderer(),
		  highlight: function(code) {
		    return hljs.highlightAuto(code).value;
		  },
		  pedantic: false,
		  gfm: true,
		  tables: true,
		  breaks: true,
		  sanitize: true,
		  smartLists: true,
		  smartypants: true,
		  xhtml: true
		})
  },
	methods:{
		getData(){
			this.$axios({
				method:"get",
				url:"../../static/"+this.routeParams+".md",
			}).then(res =>{
				console.log(res);
				this.articleDetail = res.data;
			})
		}
	},created(){
		if (sessionStorage.getItem('route') !== null) {
			this.routeParams = sessionStorage.getItem('route');
			this.getData();
		} else{
			this.getData();
		}
	},
	computed:{
		compiledMarkdown () {
		//this.articleDetail数据
		console.log(this.articleDetail);
		return marked(this.articleDetail, { sanitize: true })
		 }
	},watch: {
　　'$route': function (to, from) {
			console.log(this.$route.params.docs);
			this.routeParams = this.$route.params.docs;
		  this.getData();
	　　}
	}
}
</script>
<style scoped>
	.Index{
		position: relative;
	}
	.navBar{
		width: 20%;
		overflow: scroll;
		position: absolute;
		left: 0;
		overflow-x: hidden;
	}
	.box{
		width:80%;
		height:auto;
		overflow: scroll;
		overflow-x: hidden;
		color: #303133;
		background: #fafafa;
		position: absolute;
		right:0;
	}
	.context{
		margin:50px;
		border:none;
	}
	.cp{
		background:red;
	}
</style>



